:root {
    --color-main: #F23D4C;
    --color-secondary: #023E73;
    --color-main-alt: #D93D59;
    --color-secondary-alt: #35648C;
    --color-back: #f2f2f2;
}

body {
    font-family: 'IBM Plex Mono', monospace;
    background-color: var(--color-back);
    margin: 0;
    padding: 0;

}

#header {
    font-size: 3rem;
    text-align: center;
    color: white;
    background-color: var(--color-secondary);
    margin-bottom: 20px;
    padding: 5px;
    h1 {
        margin: 0;
    }
}

#content {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    flex-wrap: wrap;
    //border: 2px dotted black;

    .textBox {
        width: 600px;
        height: auto;
        //border: 2px dotted darkblue;
        div {
            padding: 0;
        }

        label {
            color: var(--color-main-alt);
            font-size: 2em;
        }
        .textarea {
            background-color: var(--color-back);
            border: 2px solid white;
            width: 100%;
            height: fit-content;
            min-height: 600px;

        }
    }
}